body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background-attachment: fixed;
  background: whitesmoke;
  background-repeat: no-repeat;
  font-family: "Roboto"
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Search-Bar-Form {
    height: 200px;
    background-image: -webkit-linear-gradient(bottom left, #2196F3, #80DEEA);
    background-image: linear-gradient(to top right, #2196F3, #80DEEA);
    vertical-align: text-top;
    margin-top: 10%;
    margin-left: 25%;
    margin-right: 5px;
    float: left;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}


.Search-Bar-Inputs-Container {
   
}

.Search-Bar-Input {
    padding-left: 10px;
    background-color: transparent; 
    border-style: solid; 
    border-width: 0px 0px 1px 0px; 
    border-color: white;
    margin: 10px;
    margin-top: 40px;
    outline: none;
    color: white;
    font-family: "Roboto";
    
}

.Search-Bar-Input-Submit {
    border: none;
    background: #0D47A1;
    margin-top: 10px;
    color: white;
    text-decoration: none;
    padding: 5px 20px;
    font-weight: 500;
    cursor: pointer;
    border-radius: 2px;
}

input::-webkit-input-placeholder {
    color: white;
}

input::-ms-input-placeholder {
    color: white;
}

input::placeholder {
    color: white;
}

input {
    padding-left: 10px;
    background-color: transparent; 
    border-style: solid; 
    border-width: 0px 0px 1px 0px; 
    border-color: white;
    margin: 10px;
    margin-top: 40px;
    outline: none;
    color: white;
    letter-spacing: 1px;
}

input:-webkit-autofill {
    -webkit-text-fill-color: #fff !important;
    caret-color: white;
    -webkit-transition: background-color 5000s ease-in-out 0s;
    transition: background-color 5000s ease-in-out 0s;
}
.Weather-Results-weather-container {
    
    width: 40%;
    margin-top: 10%;
    background-image: -webkit-linear-gradient(bottom right, #2196F3, #80DEEA);
    background-image: linear-gradient(to top left, #2196F3, #80DEEA);
    height: 200px;
    background-color:  white;
    vertical-align: text-center;
    
    display: inline-block;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}

.Weather-Results-weather-h1 {
    width: 75%;
    margin: auto;
    margin-left: 15%;
    height: 100%;
    text-align: left;
    color: white;

}

h1 {

    display: inline-block;
    font-size: 50px;
    margin-bottom: 0px;
}

h2 {
    font-size: 40px;
    padding-left: 5px;
    margin-bottom: 0px;
    margin:0px;
    display: inline-block;

}

h3 {
    margin: 0px;
    padding: 0px;
}

img {
    float: left;
    margin-left: 40px;
    margin-top: 40px;
     

    
      
}
